<template>
    <div>
        Welcome to Event Menu.
        <List :listShow="listShow" :items="events"/>
        <a @click="$router.push('Map')">Go Back</a>
    </div>
</template>

<script>
import List from '../components/EventList.vue'
import axios from 'axios'
    export default {
        name: "EventMenu",
        components:{
            List
        },
        data() {
            return{
                events: [],
                listShow: 10,
            }
        },
        async mounted() {
            await this.refreshEvents();
        },
        methods: {
           //Fetches Events from Server
           async refreshEvents(){
           let res = await axios.get("http://localhost:3000/events");
           this.events = res.data;
           console.log("Fetched Events: " + this.events, this.events);
           },
        }
    }
</script>

